<template>
  <div class="page__content">
    <div class="page__header">
      <h1 class="page__title">
        Уведомления
      </h1>
    </div>

    <div class="notice-list-wrapper">
      <h3 v-show="notices.length === 0">Уведомлений нет</h3>
      <div class="notice-list">
        <div
          class="notice"
          v-for="(notice, index) in notices"
          :key="notice.id"
          @click="readNotice(notice)"
        >
          <div class="notice__img" :class="{ new: notice.read_at === null }">
            <!--          <span class="notice__new" v-if="notice.new"></span>-->
            <img :src="notice.data.image" alt="photo" width="40" height="40" />
          </div>
          <div class="notice__wrap">
            <div class="notice__event">{{ notice.subject }}</div>
            <div class="notice__name">{{ notice.body }}</div>
            <div class="notice__comment">{{ notice.comment }}</div>
            <div class="notice__date">{{ notice.created_at | formatDate }}</div>
          </div>
          <button
            class="notice__btn-delete"
            @click.stop="deleteNotice(notice, index)"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="16"
              fill="currentColor"
              viewBox="0 0 16 16"
            >
              <path
                d="M15.266 15.313c-.282.28-.797.28-1.079 0L8 9.078l-6.234 6.235c-.282.28-.797.28-1.079 0-.28-.282-.28-.797 0-1.079L6.923 8 .688 1.812c-.282-.28-.282-.796 0-1.078.28-.28.796-.28 1.078 0L8 6.97 14.188.734c.28-.28.796-.28 1.078 0 .28.282.28.797 0 1.079L9.03 8l6.235 6.234c.28.282.28.797 0 1.079z"
              />
            </svg>
          </button>
        </div>
      </div>
      <button
        class="playbill__more-btn button-warning"
        v-show="showMore"
        @click="loadMore"
      >
        показать ещё
      </button>
    </div>
  </div>
</template>

<script>
import mixin from "../mixin/mixin";
import axios from "axios";

export default {
  name: "NotificationPage",
  mixins: [mixin],
  data() {
    return {
      notices: [],
      pageData: [],
      currentPage: 1,
      perPage: 20
    };
  },
  computed: {
    showMore() {
      return this.pageData.pageCount > this.pageData.currentPage;
    }
  },
  mounted() {
    this.loadData();
  },
  methods: {
    async loadData() {
      let load = this.$loading.show();

      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/notification/list`);
      url.searchParams.set("per-page", this.perPage);
      url.searchParams.set("page", this.currentPage);

      await axios
        .get(url)
        .then(response => {
          if (this.currentPage > 1) {
            this.notices.push(...response.data._items);
          } else {
            this.notices = response.data._items;
          }

          this.pageData = response.data._meta;
          load.hide();
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
            load.hide();
          }
          this.$toasted.global.error(error.response.data.message);
          load.hide();
        });
    },
    loadMore() {
      if (this.pageData.pageCount > this.currentPage) {
        this.currentPage++;
      }
      this.loadData();
    },
    async readNotice(notice) {
      let url = new URL(
        `${process.env.VUE_APP_API_URL}/v1/notification/mark-read`
      );
      url.searchParams.set("id", notice.id);
      await axios
        .patch(url)
        .then(response => {
          notice.read_at = response.read_at;

          switch (notice.data.type) {
            case "event":
              this.$router.push(`/event/${notice.data.entity_id}`);
              break;
            case "event_comment":
              this.$router.push(`/event/${notice.data.entity_id}`);
              break;
            case "startup":
              this.$router.push(`/art-startup/${notice.data.entity_id}`);
              break;
            case "startup_comment":
              this.$router.push(`/art-startup/${notice.data.entity_id}`);
              break;
            case "post":
              this.$router.push(`/news-view/${notice.data.entity_id}`);
              break;
            case "resident_request":
              this.$router.push(`/resident-profile`);
              break;
            case "partner_request":
              this.$router.push(`/partner-profile`);
              break;
          }

          console.log(notice.data.type, `/event/${notice.data.entity_id}`);
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
          }
          this.$toasted.global.error(error.response.data.message);
        });
    },
    async deleteNotice(notice, index) {
      let url = new URL(
        `${process.env.VUE_APP_API_URL}/v1/notification/delete`
      );
      url.searchParams.set("id", notice.id);
      await axios
        .delete(url)
        .then(() => {
          this.notices.splice(index, 1);
          this.$toasted.global.error("Уведомление удалено ");
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
          }
          this.$toasted.global.error(error.response.data.message);
        });
    }
  }
};
</script>

<style scoped></style>
